.addressForm{
  
    width:80%;
    margin-left:8%;
    /* background-color: red; */
    margin-top: 10px;
    display:flex;
    flex-direction: column;
    gap: 25px;
}

.addressForm input{
   margin-left: 2px;
    width:98%;
    height:30px;
    border: 1px solid #623381;
    padding-left: 10px;
}
.addressForm>div>select{
    padding-left: 10px;
    margin-left: 2px;
    width:100%;
    height:30px;
    border: 1px solid #623381;
}


.lastone{
    display:flex;
}
.lastone>div:nth-child(1){
    width:19%;
   
}
.lastone>div:nth-child(2){
    width:20%;
 

 
}
.lastone>div:nth-child(3){
    width:60%;
}



.saveButton{
    width: 100%;
    height: 80px;
    background-color: #f6f6f6;
    /* opacity: 0.8; */
    position: fixed;
    bottom: 0;
    opacity: 0.9;
  }
  .saveButton>div{
    width: 20%;
    height: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    gap: 10%;
  }

  .saveButton>div>button:nth-child(2){
    width: 100px;
    margin: auto;
    background-color: #f22971;
    color: white;
    height: 40px;
    border: none;
    border-radius: 3px;
    opacity: 1;
    z-index: 1;
   
  }

  .Addheading{
      margin-left: 30px;
  }
  .Addheading1{
    margin-left: 50px;
    color: grey;
}

  .saveButton>div>button:nth-child(1){
    width: 100px;
    margin: auto;
    background-color: #fff;
    color: grey;
    height: 40px;
    border: 1px solid grey;
    border-radius: 3px;
    opacity: 1;
    z-index: 1;
   
  }

  .checkoutbagbtn1{
    background-color: #623380;
    font-weight: bold;
    font-size: 18px !important;
    text-align: center;
    color: #fff;
    border-radius: 0;
    text-transform: uppercase;
    padding: 8px;
    width: 60%;
    border-color: #ccc;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    
}