.tag{
    width: 50px;
    height: 50px;
    background-image: linear-gradient(to top, #CB1D65 0%, #662C90 100%);
    color: white;
    border-radius: 30px;
    display: grid;
    place-items: center;
    
}
.tag > p{
    font-size: small;
    margin: 0px;
}
.tag > h5{
    margin: 0px;
    font-size: 20px;
    font-weight: 550;
}