.footer {
  margin-top: 50px;
  letter-spacing: 0.09em;
  font-size: 14px;
  font-family: Lato, sans-serif;
  display: block;
  font-family: Lato, sans-serif;
}
.socialMedia {
  background: url(https://a.cdnsbn.com/images/common/2016footer.jpg) center top
    no-repeat #702f80;
  color: #fff;
  padding: 32px 0;
}
.container {
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
  display: block;
}
.row {
  margin-left: -7.5px;
  margin-right: -7.5px;
  /* display: block; */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 100px;
}

@media(max-width:900px){

  .row{
    grid-template-columns: repeat(2,1fr);

  }
  
}
.connect {
  font-size: 24px;
  margin: 5px 0 25px;
  letter-spacing: 0.015em;
  font-family: Lato, sans-serif;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
  margin-block-start: 1.67em;
  margin-block-end: 1.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  text-align: left;
}
.signup {
  text-align: left;
  color: #fff;
}
.inputGp {
  margin-bottom: 25px;
  padding-top: 14px;
  position: relative;
  display: table;
  border-collapse: separate;
  position: relative;
}
.signupInp {
  height: 50px;
  font-size: 16px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  position: relative;
  z-index: 2;
  float: left;
  width: 90%;
  margin-bottom: 0;
  display: table-cell;
  padding: 6px 12px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #623380;
  box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  cursor: text;
}
.signBut {
  position: absolute;
  top: 20%;
  left: 99%;
  margin-top: 0;
  font-size: 0;
  white-space: nowrap;
  width: 1%;
  display: table-cell;
  border-collapse: separate;
  text-align: left;
}
.signupbtnref {
  position: relative;
  background-color: #52246d;
  color: #fff;
  height: 52px;
  padding: 4px 10px;
  line-height: 50px;
  font-weight: 700;
  text-transform: uppercase;
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  font-size: 13px;
  border-radius: 4px;
  user-select: none;
  text-decoration: none;
  text-align: left;
}
.signupbtnref:hover {
  background-color: #fff;
  color: #52246d;
}
