.gridmainbox {
  display: grid;
  grid-template-columns: 18% 15% 15% 15% 25%;
  letter-spacing: 0.09em;
  font-size: 14px;
  gap: 4%;

}

@media (max-width:1080px){


  .gridmainbox{

    grid-template-columns:repeat(3,1fr) ;

  }
 
}







.gridmainbox1grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
}

.gridmainbox1grid > img {
  width: 62px;
  aspect-ratio: auto 62 / 43;
  height: 43px;
}
.gridmainbox2 {
  font-size: 20px;
